import {
  Box,
  Card,
  Flex,
  Grid,
  GridItem,
  ListItem,
  Text,
  UnorderedList,
  VStack,
} from "@chakra-ui/react";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { IntroSizingInfo, ProjectProfile } from "../utils/types";
import IntroCard from "../components/IntroCard";
import { getYdAndM } from "@tanyoknits/shared";
import { sizes } from "../projects/pine-pullover/pattern";
import IntroSizingTable from "../components/IntroSizingTable";

export default function Intro() {
  const [profile, setProfile] = useState<ProjectProfile | null>(null);
  const [introSizing, setIntroSizing] = useState<IntroSizingInfo | null>(null);
  const param = useParams();

  // Load project info
  useEffect(() => {
    (async () => {
      const profile = await import(`../projects/${param.projectID}/profile`);
      setProfile(profile.default);
      const patternInfo = await import(
        `../projects/${param.projectID}/pattern`
      );
      setIntroSizing(patternInfo.default);
    })();
  }, [param]);

  if (profile == null || introSizing == null) {
    return null;
  }
  const { photos, yarn, needles, guage, notions, difficulty } = profile;

  return (
    <Grid
      p={4}
      templateRows="repeat(1, 1fr)"
      templateColumns="repeat(5, 1fr)"
      gap={4}
    >
      <GridItem colSpan={1}>
        <VStack spacing={4} width="xs">
          {photos.map((photo, i) => (
            <Card
              key={i}
              variant="elevated"
              height="xs"
              style={{
                width: "100%",
                background: `url(${process.env.PUBLIC_URL}/photos/${photo})`,
                backgroundSize: "cover",
              }}
            />
          ))}
        </VStack>
      </GridItem>
      <GridItem colSpan={4}>
        <Flex direction={["column", "row"]} flexFlow="wrap" gap={4}>
          <IntroCard title=" Yarn">
            <>
              <a href={yarn.url} target="_parent">
                <Text variant="link">{yarn.line}</Text>
              </a>
              by {yarn.maker}
              <Text>Sample color: {yarn.color}</Text>
              <Text>
                1 {yarn.shape} = {getYdAndM(yarn.yards)} / {yarn.grams}g
              </Text>
            </>
          </IntroCard>
          <IntroCard title=" Needles">
            <UnorderedList>
              {needles.map((needle, i) => (
                <ListItem key={i}>
                  {needle.size} {needle.type} - {needle.usage}
                </ListItem>
              ))}
            </UnorderedList>
          </IntroCard>
          <IntroCard title=" Guage">
            <Text>
              {guage.sts_count} sts and {guage.row_count} rows = 4" / 10cm
            </Text>
            <Text>
              in {guage.sts} with {guage.needle}
            </Text>
          </IntroCard>
          <IntroCard title=" Notions">
            <UnorderedList>
              {notions.map((notion, i) => (
                <ListItem key={i}>{notion}</ListItem>
              ))}
            </UnorderedList>
          </IntroCard>
          <IntroCard title=" Difficulty">
            <Text>{difficulty}</Text>
          </IntroCard>
          <IntroCard title=" Sizing & Measurement">
            <IntroSizingTable
              sizes={sizes}
              introSizing={introSizing}
              yarn={yarn}
            />
          </IntroCard>
        </Flex>
      </GridItem>
    </Grid>
  );
}
