import { Heading, VStack, Text, HStack } from "@chakra-ui/react";
import { Link } from "react-router-dom";
import { allProjects } from "../projects/list";
import { ProjectCard } from "@tanyoknits/shared";
import Footer from "./Footer";

export default function Main() {
  return (
    <VStack pt={10} pb={10}>
      <Heading as="h1">Interactive knitting patterns</Heading>
      <Heading as="h2" size="md" fontWeight={400} color="gray.600">
        Follow stitches in both visual and written instructions
      </Heading>
      <Text fontSize="xl">by tanyoknits</Text>
      <HStack pt={5}>
        {Object.keys(allProjects).map((project, i) => {
          const { name, features, sizes } = allProjects[project];
          const photo = `${project}-01.jpg`;
          return (
            <Link key={i} to={`${project}`}>
              <ProjectCard
                title={name}
                list={features.concat(` ${sizes.length} sizes`)}
                photo={photo}
              />
            </Link>
          );
        })}
      </HStack>
      <Footer mode="light" />
    </VStack>
  );
}
