import { Table, Td, Tr, Th, Tbody } from "@chakra-ui/react";
import { Size } from "../utils/sizes";
import { getCapitalized, getInchAndCm, getYdAndM } from "@tanyoknits/shared";
import { IntroSizingInfo, YarnInfo } from "../utils/types";
import { css, cx } from "@linaria/core";

const styles = {
  table: css`
    th {
      border-color: transparent;
      padding-left: 0;
      padding-bottom: 4px;
    }
    td {
      padding-left: 0;
      padding-top: 8px;
      padding-bottom: 8px;
      border-color: var(--oc-gray-4);
    }
  `,
  newLine: css`
    white-space: pre-line;
  `,
};

interface Props {
  sizes: Size[];
  introSizing: IntroSizingInfo;
  yarn: YarnInfo;
}

export default function introSizing({ sizes, introSizing, yarn }: Props) {
  const { totalSts, bodyParts, foParts, baseSizing } = introSizing;
  const yarnBySize = sizes.map((size, i) => {
    const grams =
      (baseSizing.grams * totalSts[i]) /
      totalSts[sizes.indexOf(baseSizing.size) ?? 0];
    const yards = (yarn.yards * grams) / yarn.grams;
    return { grams, yards, count: Math.ceil(grams / yarn.grams) };
  });

  return (
    <Table className={cx(styles.table)}>
      <Tbody>
        <Tr>
          <Td />
          {sizes.map((s, i) => (
            <Td key={i} fontWeight={600} isNumeric>
              {s}
            </Td>
          ))}
        </Tr>
        <Tr>
          <Th>Body size</Th>
        </Tr>
        {bodyParts.map((p, i) => (
          <Tr key={i}>
            <Td>{getCapitalized(p.part)}</Td>
            {p.sizes.map((size, j) => (
              <Td key={j} isNumeric className={cx(styles.newLine)}>
                {getInchAndCm(size, "\n")}
              </Td>
            ))}
          </Tr>
        ))}
        <Tr>
          <Th>Finished measurement</Th>
        </Tr>
        {foParts.map((p, i) => (
          <Tr key={i}>
            <Td>{getCapitalized(p.part)}</Td>
            {p.sizes.map((size, j) => (
              <Td key={j} isNumeric className={cx(styles.newLine)}>
                {getInchAndCm(size, "\n")}
              </Td>
            ))}
          </Tr>
        ))}
        <Tr>
          <Th>Yarn needed</Th>
        </Tr>
        <Tr>
          <Td>{yarn.shape}(s)</Td>
          {yarnBySize.map((size, i) => (
            <Td key={i} isNumeric className={cx(styles.newLine)}>
              {size.count}
            </Td>
          ))}
        </Tr>
        <Tr>
          <Td>Yard</Td>
          {yarnBySize.map((size, i) => (
            <Td key={i} isNumeric className={cx(styles.newLine)}>
              {getYdAndM(size.yards, "\n")}
            </Td>
          ))}
        </Tr>
        <Tr>
          <Td>Weight</Td>
          {yarnBySize.map((size, i) => (
            <Td key={i} isNumeric className={cx(styles.newLine)}>
              {Math.round(size.grams)}g
            </Td>
          ))}
        </Tr>
        {/* <Tr>
          <Td padding={0}>Bust circumference</Td>
          {sizes.map((s, i) => (
            <Td key={i} textAlign="center">
              {getInchAndCm(baseBodySizing?.[s]?.bust ?? 0)}
            </Td>
          ))}
        </Tr>
         <Tr>
          <Th padding={0}>Finished measurement</Th>
        </Tr>
        <Tr>
          <Td padding={0}>Bust circumference</Td>
          {measurements.map((m, i) => (
            <Td key={i} textAlign="center">
              {getInchAndCm(m.bust_circumference ?? 0)}
            </Td>
          ))}
        </Tr>
        <Tr>
          <Td padding={0}>Total length</Td>
          {measurements.map((m, i) => (
            <Td key={i} textAlign="center">
              {getInchAndCm(
                (m.underarm_to_hem_length ?? 0) +
                  (m.hem ?? 0) +
                  (m.neck_band_height ?? 0) +
                  (m.york_length ?? 0)
              )}
            </Td>
          ))}
        </Tr>
        <Tr>
          <Td padding={0}>Sleeve length from underarm</Td>
          {measurements.map((m, i) => (
            <Td key={i} textAlign="center">
              {getInchAndCm(m.sleeve_length_from_underarm ?? 0)}
            </Td>
          ))}
        </Tr>
        <Tr>
          <Th padding={0}>Yarn needed</Th>
        </Tr>
        <Tr>
          <Td padding={0}>{yarn.shape} count</Td>
          {sizes.map((_, i) => (
            <Td key={i} textAlign="center">
              {totalSts[i]}
            </Td>
          ))}
        </Tr> */}
      </Tbody>
    </Table>
  );
}
